@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

html {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 15px;
}

body {
  margin: 0px;
  background-color: #FBFBFD;

  color: #333333;

  text-align: center;
}

.dark {
  background-color: #121212;
  color: #ffffff;
}

ul {
  padding: 0px;
  list-style: none;
}

h3 {
  font-size: 1.5rem;
  padding-bottom: 5%;
  border-bottom: 1px solid gray;
}

.component {
  width: 600px;
  margin: auto;

  margin-top: 10%;
  margin-bottom: 10%;

  padding: 2%;
}

/*Responsive*/
@media screen and (min-width: 768px) and (max-width: 1023px) {
  h1 {
      font-size: 2.5rem;
  }

  .component {
    margin-top: 15%;
    margin-bottom: 10%;    
    width: 70%;
  }
}
@media screen and (max-width: 767px) {
  html {
      font-size: 14px;
  }

  h1 {
      font-size: 2rem;
  }

  .component {
    margin-top: 25%;
    margin-bottom: 10%;    
    width: 70%;
  }
}