.navigation {
    position: fixed;
    z-index: 3;
    width: 100%;
    top: 0px;
    padding-top: 1%;
    padding-bottom: 1%; 

    background-color: #333333;
}

.dark {
    background-color: #121212;
    color: #ffffff;
  }

.navigation-list {
    list-style: none;
    text-align: center;

    display: grid;
    grid-template-columns: auto auto auto auto;

    margin: 0px;
    -webkit-align-items: center;
            align-items: center;
}

.navigation-list li{
    text-align: center;
    vertical-align: middle;
    color: #FBFBFD;
    text-decoration: none;
}

.navigation-list a {
    color: #FBFBFD;
    text-decoration: none;
}

.navigation-list #logo {
    font-size: 1.5rem;
    margin: 0px;
}

.navigation-list #logo:hover{
    font-weight: bold;
    color: #ffffff;
