}

.navigation-list .navigation-user_picture {
    width: 30px;
    height: 30px;
    /*background-color: #333333;
    background-image: url("../../Komputer.png");
    background-position: center;
    background-size: cover;*/
    border-radius: 50%;
    margin: auto;
    vertical-align: middle;
}

.navigation-list .navigation-user-name {
